"use client";

import { useState } from "react";
import { Briefcase, User, GraduationCap, Users, MapPin } from "lucide-react";
import LayananHeader from "@/components/admin/layanan/LayananHeader";
import LayananTabs from "@/components/admin/layanan/LayananTabs";
import LayananGrid from "@/components/admin/layanan/LayananGrid";
import RiwayatKegiatan from "@/components/admin/layanan/RiwayatKegiatan";

export default function DaftarLayananAdmin() {
  const [activeTab, setActiveTab] = useState<"layanan" | "riwayat">("layanan");

  const services = [
    {
      id: 1,
      icon: <Briefcase size={48} strokeWidth={1.5} />,
      title: "Kunjungan",
      description: "Program PKL untuk Siswa/ Mahasiswa di Industri Kopi",
      duration: "2 - 4 Bulan",
      price: "50 ribu",
      route: "/admin/layanan/daftar/1",
    },
    {
      id: 2,
      icon: <User size={48} strokeWidth={1.5} />,
      title: "Kunjungan",
      description: "Program PKL untuk Siswa/ Mahasiswa di Industri Kopi",
      duration: "2 - 4 Bulan",
      price: "50 ribu",
      route: "/admin/layanan/daftar/2",
    },
    {
      id: 3,
      icon: <GraduationCap size={48} strokeWidth={1.5} />,
      title: "Kunjungan",
      description: "Program PKL untuk Siswa/ Mahasiswa di Industri Kopi",
      duration: "2 - 4 Bulan",
      price: "50 ribu",
      route: "/admin/layanan/daftar/3",
    },
    {
      id: 4,
      icon: <Users size={48} strokeWidth={1.5} />,
      title: "Kunjungan",
      description: "Program PKL untuk Siswa/ Mahasiswa di Industri Kopi",
      duration: "2 - 4 Bulan",
      price: "50 ribu",
      route: "/admin/layanan/daftar/4",
    },
    {
      id: 5,
      icon: <MapPin size={48} strokeWidth={1.5} />,
      title: "Kunjungan",
      description: "Program PKL untuk Siswa/ Mahasiswa di Industri Kopi",
      duration: "2 - 4 Bulan",
      price: "50 ribu",
      route: "/admin/layanan/daftar/5",
    },
  ];

  return (
    <div className="min-h-screen bg-gray-50">
      <LayananHeader />
      <LayananTabs activeTab={activeTab} onTabChange={setActiveTab} />

      {/* Content */}
      <div className="p-6">
        {activeTab === "layanan" ? (
          <LayananGrid services={services} />
        ) : (
          <RiwayatKegiatan />
        )}
      </div>
    </div>
  );
}
