"use client";
import { Clock, User, ChevronRight } from "lucide-react";
import Link from "next/link";

interface ServiceCardAdminProps {
  id: number;
  icon: React.ReactNode;
  title: string;
  description: string;
  duration: string;
  price: string;
  route: string;
}

export default function ServiceCardAdmin({
  icon,
  title,
  description,
  duration,
  price,
  route,
}: ServiceCardAdminProps) {
  return (
    <div className="bg-white rounded-lg shadow-sm hover:shadow-md transition-all duration-300 p-6 flex flex-col border border-gray-200">
      {/* Icon */}
      <div className="flex justify-start mb-4">
        <div className="text-primary bg-amber-50 p-3 rounded-lg">{icon}</div>
      </div>

      {/* Title with Chevron */}
      <div className="flex items-start justify-between mb-3">
        <h3 className="text-base font-semibold text-gray-900">{title}</h3>
        <ChevronRight
          className="text-gray-400 flex-shrink-0 mt-0.5"
          size={18}
        />
      </div>

      {/* Description */}
      <p className="text-gray-500 text-xs mb-4 flex-grow leading-relaxed">
        {description}
      </p>

      {/* Info Row - Combined in one line */}
      <div className="mb-4 text-xs">
        <span className="border border-gray-300 px-3 py-1.5 rounded-md inline-flex items-center gap-1.5 text-gray-600">
          <Clock size={12} />
          {duration}
          <span className="mx-1">â€¢</span>
          <User size={12} />
          {price}
        </span>
      </div>

      {/* Action Buttons */}
      <div className="flex gap-2 text-xs">
        <button className="flex-1 border border-gray-300 text-gray-700 py-2 px-3 rounded-md font-medium hover:bg-gray-50 transition-colors duration-200">
          Lihat
        </button>
        <Link href={route} className="flex-1">
          <button className="w-full bg-primary text-white py-2 px-3 rounded-md font-medium hover:bg-primary/90 transition-colors duration-200">
            Edit
          </button>
        </Link>
        <button className="flex-1 border border-red-300 text-red-600 py-2 px-3 rounded-md font-medium hover:bg-red-50 transition-colors duration-200">
          Nonaktifkan
        </button>
      </div>
    </div>
  );
}
