import ServiceCardAdmin from "@/components/layanan/ServiceCardAdmin";

interface Service {
  id: number;
  icon: React.ReactNode;
  title: string;
  description: string;
  duration: string;
  price: string;
  route: string;
}

interface LayananGridProps {
  services: Service[];
}

export default function LayananGrid({ services }: LayananGridProps) {
  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
      {services.map((service) => (
        <ServiceCardAdmin
          key={service.id}
          id={service.id}
          icon={service.icon}
          title={service.title}
          description={service.description}
          duration={service.duration}
          price={service.price}
          route={service.route}
        />
      ))}
    </div>
  );
}
